/* Styles Index - Styles Index */

/* Import TailwindCSS */
@import "./tailwind.css";

/* Import remaining styles */

/* Global styles reset and base styles */
* {
  box-sizing: border-box;
}

/* Global font size adjustments */
html {
  font-size: 16px; /* Base font size */
}

body {
  background: linear-gradient(to bottom right, #f0f5ff, #e6fffb);
  min-height: 100vh;
  font-size: 16px; /* Increase base body font size */
  line-height: 1.6;
}

/* Increase font sizes for better readability */
.ant-statistic-title {
  font-size: 16px !important; /* Increase from default 14px */
  font-weight: 500;
}

.ant-statistic-content {
  font-size: 28px !important; /* Increase from default 24px */
  font-weight: 700;
}

/* Increase card content font sizes */
.ant-card-body {
  font-size: 16px !important;
}

/* Ensure statistics cards are not affected */
.ant-card .ant-statistic .ant-card-body {
  font-size: inherit !important;
}

/* Increase table font sizes */
.ant-table {
  font-size: 16px;
}

.ant-table-thead > tr > th {
  font-size: 16px;
  font-weight: 600;
}

.ant-table-tbody > tr > td {
  font-size: 16px;
}

/* Increase button font sizes */
.ant-btn {
  font-size: 16px;
}

.ant-btn-sm {
  font-size: 14px;
}

.ant-btn-lg {
  font-size: 18px;
}

/* Increase input font sizes */
.ant-input {
  font-size: 16px;
}

.ant-input-lg {
  font-size: 18px;
}

/* Increase form label font sizes */
.ant-form-item-label > label {
  font-size: 16px;
  font-weight: 500;
}

/* Increase divider text font sizes */
.ant-divider-inner-text {
  font-size: 18px;
  font-weight: 600;
}

/* Increase modal font sizes */
.ant-modal-title {
  font-size: 20px;
  font-weight: 600;
}

.ant-modal-content {
  font-size: 16px;
}

/* Increase dropdown menu font sizes */
.ant-dropdown-menu-item {
  font-size: 16px;
}

/* Increase pagination font sizes */
.ant-pagination-item {
  font-size: 16px;
}

.ant-pagination-prev,
.ant-pagination-next {
  font-size: 16px;
}

/* Increase tooltip font sizes */
.ant-tooltip-inner {
  font-size: 14px;
}

/* Increase message font sizes */
.ant-message-notice-content {
  font-size: 16px;
}

/* Increase notification font sizes */
.ant-notification-notice-message {
  font-size: 16px;
  font-weight: 500;
}

.ant-notification-notice-description {
  font-size: 14px;
}

/* Increase progress text font sizes */
.ant-progress-text {
  font-size: 16px;
  font-weight: 500;
}

/* Increase tag font sizes */
.ant-tag {
  font-size: 14px;
}

/* Increase badge font sizes */
.ant-badge-count {
  font-size: 12px;
}

/* Increase alert font sizes */
.ant-alert-message {
  font-size: 16px;
  font-weight: 500;
}

.ant-alert-description {
  font-size: 14px;
}

/* Increase list font sizes */
.ant-list-item {
  font-size: 16px;
}

.ant-list-item-meta-title {
  font-size: 16px;
  font-weight: 500;
}

.ant-list-item-meta-description {
  font-size: 14px;
}

/* Increase menu font sizes */
.ant-menu-item {
  font-size: 16px;
}

.ant-menu-submenu-title {
  font-size: 16px;
}

/* Increase tabs font sizes */
.ant-tabs-tab {
  font-size: 16px;
}

.ant-tabs-tab-btn {
  font-size: 16px;
}

/* Increase breadcrumb font sizes */
.ant-breadcrumb {
  font-size: 16px;
}

/* Increase steps font sizes */
.ant-steps-item-title {
  font-size: 16px;
  font-weight: 500;
}

.ant-steps-item-description {
  font-size: 14px;
}

/* Increase timeline font sizes */
.ant-timeline-item-content {
  font-size: 16px;
}

/* Increase comment font sizes */
.ant-comment-content-author-name {
  font-size: 16px;
  font-weight: 500;
}

.ant-comment-content-detail {
  font-size: 16px;
}

/* Increase avatar font sizes */
.ant-avatar-string {
  font-size: 16px;
  font-weight: 500;
}

/* Increase typography font sizes */
.ant-typography {
  font-size: 16px;
}

.ant-typography h1 {
  font-size: 32px;
  font-weight: 700;
}

.ant-typography h2 {
  font-size: 28px;
  font-weight: 600;
}

.ant-typography h3 {
  font-size: 24px;
  font-weight: 600;
}

.ant-typography h4 {
  font-size: 20px;
  font-weight: 500;
}

.ant-typography h5 {
  font-size: 18px;
  font-weight: 500;
}

/* Scrollbar styles */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Selection text styles */
::selection {
  background: #bae6fd; /* blue-200 */
  color: #075985; /* blue-800 */
}

/* Focus styles */
:focus {
  outline: 2px solid #3b82f6; /* blue-500 */
  outline-offset: 2px;
}

/* Link styles */
a {
  color: #2563eb; /* blue-600 */
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  color: #1d4ed8; /* blue-700 */
  text-decoration: underline;
}

/* Button base styles */
button {
  cursor: pointer;
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Input base styles */
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

/* Image styles */
img {
  max-width: 100%;
  height: auto;
}

/* Table styles */
table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  padding: 8px 12px;
  text-align: left;
  border-bottom: 1px solid #e8e8e8;
}

th {
  background-color: #fafafa;
  font-weight: 600;
}

/* List styles */
ul,
ol {
  padding-left: 20px;
}

li {
  margin-bottom: 4px;
}

/* Blockquote styles */
blockquote {
  margin: 0;
  padding: 12px 16px;
  border-left: 4px solid #3b82f6; /* blue-500 */
  background-color: #eff6ff; /* blue-50 */
  font-style: italic;
}

/* Code block styles */
pre {
  background-color: #f6f8fa;
  border: 1px solid #e1e4e8;
  border-radius: 6px;
  padding: 16px;
  overflow-x: auto;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 14px;
  line-height: 1.45;
}

code {
  background-color: #f6f8fa;
  border: 1px solid #e1e4e8;
  border-radius: 3px;
  padding: 2px 4px;
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, monospace;
  font-size: 85%;
}

pre code {
  background-color: transparent;
  border: none;
  padding: 0;
}

/* Horizontal divider styles */
hr {
  border: none;
  height: 1px;
  background: linear-gradient(
    to right,
    transparent,
    #d4d4d8,
    transparent
  ); /* gray-300 */
  margin: 24px 0;
}

/* Responsive image */
.responsive-img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Clear float */
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/* Text ellipsis */
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-ellipsis-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.text-ellipsis-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }

  body {
    background: white !important;
  }

  .page-container {
    background: white !important;
    padding: 0 !important;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  /* 高对比度模式现在通过 Tailwind CSS 的 prefers-contrast 变体处理 */
}

/* Reduced animation mode */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Dark mode support (future extension) */
@media (prefers-color-scheme: dark) {
  /* Dark mode styles can be added here */
}

/* Accessibility support */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Focus visibility */
.focus-visible:focus {
  outline: 2px solid #3b82f6; /* blue-500 */
  outline-offset: 2px;
}

/* Skip link */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #2563eb; /* blue-600 */
  color: white;
  padding: 8px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000;
}

.skip-link:focus {
  top: 6px;
}
