/* 
 * 已移除所有 CSS 变量定义
 * 现在统一使用 Tailwind CSS 系统
 * 
 * 颜色映射参考：
 * - primary-500 -> blue-500 (#3b82f6)
 * - primary-600 -> blue-600 (#2563eb)
 * - success-500 -> green-500 (#10b981)
 * - warning-500 -> yellow-500 (#f59e0b)
 * - error-500 -> red-500 (#ef4444)
 */

body {
  margin: 0;
  font-family:
    -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:
    source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* 
 * 已移除所有 CSS 变量相关的样式
 * 现在使用 Tailwind CSS 类名替代
 */

/* Animation for Popconfirm */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
